import React from "react"

class CProteccionHack extends React.Component{
    render(){
        return(
            <div className="row justify-content-lg-end">
                <div className="col-12 col-lg-8 col-xl-9 p-5 parrafo_post">
                    <p class="p-2">Existen maneras de proteger tu información de estas personas llamadas hackers aunque  no todos tienen la mentalidad de hacer el MAL, existe una gran parte de hackers que prefieren ayudar a la comunidad. Aun así veamos algunas reglas para estar seguros.</p>
	  	   	        <h5 class="text-center">Conexión Segura</h5>
	  	   	        <p class="p-1"><strong>Https:</strong> cuando navegas por internet y entras a un sitio web asegúrate de tener activado el protocolo HTTPS ya que es utilizado para encriptar la información del usuario.</p>
	  	   	        <p class="p-1"><strong>APP:</strong> las aplicaciones descargadas e instaladas que no forman parte de una tienda como la <b>Play Store</b>, puede ser un método de ataque para tu información.</p>
	  	   	        <p class="p-1"><strong>WIFI:</strong> a la hora de conectarte a una red wifi asegúrate de que tenga <b>WPA2</b> ya que son métodos de encriptación muy útiles, si es una red pública probable mente no lo contenga, esto quiere decir que tu información puede ser abstraída.</p>
	  	   	        <p class="p-1"><strong>INCOGNITO:</strong> Es muy fácil de utilizar ya que todo los navegadores lo tienen, esto permite navegar en sitios con el fin de eliminar <b>cookies o historial</b> al momento que cierres esa pestaña.</p>
	  	   	        <h5 class="text-center">Contraseña</h5>
	  	   	        <p class="p-1"><strong>Contraseña segura:</strong> Al momento de pensar un contraseña para tu cuenta asegúrate que sea de forma aleatoria es decir que no sea la misma contraseña de tus otras cuentas evita escribir como número telefónico, tu nombre, o nombre de algún pariente, o novia. </p>
	  	   	        <p class="p-1"><strong>Fecha de expiración:</strong> cada cierto tiempo asegúrate de cambiar tu contraseña  puede ser cada 3 meses  o cada 5 meses.</p>
	  	   	        <p class="p-1">Ten en cuenta que una contraseña nunca es suficiente ya que puede ser robada fácilmente, es por eso que es necesario tener otros métodos como Dibujos de <b>patrones</b>, huellas digitales.</p>
	  	   	        <h5 class="text-center">Email</h5>
	  	   	        <p class="p-1"><strong>Correos seguros:</strong> cuando te llegue un mensaje al correo sobre alguna promoción o anuncio debes de tener mucho cuidado ya que muchos contiene <b>enlaces</b> o <b>link</b> que te redirigen así sitios no seguros o sitios falsos.</p>
				    <h5 class="text-center">Actualizaciones</h5>
				    <div class="row">
	   	   	   	        <div class="col-12 col-md-6">
	   	   	   		        <p class="p-1">Mantente al día con las actualizaciones de tu dispositivo o de aplicaciones ya que te permitirán estar mucho más seguro de los ataques, ya que cada vez se descubren nuevos hoyos de <b>vulnerabilidad</b> en los sistemas.</p>
	   	   	   	        </div>

	   	   	   	        <div class="col-12 col-md-6">
	   	   	   		        <div id="sabias">
	   	   	   			        <h6 class="text-center">¿Sabias esto?</h6>
	   	   	   			        <p class="p-2"> Si tienes una mala administración de tu perfil en las redes sociales, los extraños pueden saber mucho sobre ti así que evita compartir información, fotografías y ubicaciones a desconocidos. Con esto también se refiere a no aceptar como amigo a personas desconocidas.</p>
	   	   	   		        </div>
	   	   	   	        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default CProteccionHack;